    } */
    
    .backButton {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 10;
    }
    
    #list-tab-hide {
        height: calc(100vh - 55px);
        background: #fff;
        padding: 0;
    }
    
    #frame #sidePanelInbox {
        background: #fff;
        color: #f5f5f5;
        overflow: hidden;
        position: relative;
        height: calc(100vh - 55px);
    }
    
