        #frame #sidePanelInbox #profile {
            width: 100%;
            margin: 0 auto;
            padding: 5px 0 0 0;
            background: #32465a;
        }
    }
    
    #frame #sidePanelInbox #profile.expanded .wrap {
        height: 210px;
        line-height: initial;
    }
    
    #frame #sidePanelInbox #profile.expanded .wrap p {
        margin-top: 20px;
    }
    
